@import "../../../scss/utils/variables";
@import "../../../scss/utils/mixins";


.formState {
  hr {
    height: 2px;
    background: rgba(39, 59, 74, 0.1);
    border-radius: 1px;
    margin-bottom: 30px;
  }
}

.formStateContent {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 30px;
  div.formStateDiv:nth-child(odd) {
    width: 50%;
  }
  div.formStateDiv:nth-child(even) {
    width: 45%;
  }
}


.formMain {
  display: flex;
  flex-direction: column;
  width: 43%;
}

.formInputWithLabel {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.formInputLabel {
  font-weight: 600;
  font-size: 20px;
  color: #273B4A;
  margin-bottom: 10px;
}

.formInput {
  outline: none;
  border: 2px solid rgba(49, 67, 81, 0.1);
  border-radius: 6px;
  font-size: 18px;
  color: rgba(49, 67, 81, 0.75);
  padding: 12px 25px;
  width: 100%;
}

.formInput::placeholder {
  font-size: 18px;
  color: rgba(49, 67, 81, 0.75);
}

.formButton {
  width: 100%;
  font-weight: bold;
  font-size: 18px;
  text-transform: uppercase;
  color: #FFF;
  background: #3760EF;
  border-radius: 6px;
  outline: none;
  border: none;
  padding: 15px 0;
  cursor: pointer;
}

.title {
  display: flex;
  margin-top: 50px;
}

.formCounter {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #3760EF;
  border-radius: 100%;
  font-size: 30px;
  color: #FFF;
  width: 46px;
  height: 46px;
  margin-right: 20px;
}

.mainTitleText {
  font-size: 40px;
  color: #000;
}

.additionalData {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.additionalDataIcon {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.additionalDataText {
  font-size: 14px;
  color: rgba(39, 59, 74, 0.5);
}

.formInputWithSelect {
  display: flex;
  select, input {
    border: none;
    outline: none;
    background: #FFF;
  }
}

.additionalInfo {
  padding: 40px;
  border: 2px solid rgba(39, 59, 74, 0.1);
  border-radius: 5px;
  max-width: 40%;
  hr {
    height: 2px;
    background: rgba(39, 59, 74, 0.1);
    border-radius: 1px;
    margin: 30px 0;
  }
}

.additionalInfoTitle {
  font-weight: 500;
  font-size: 24px;
  color: #000;
}

.list {
  padding-left: 20px;
  li {
    font-size: 18px;
    color: rgba(49, 67, 81, 0.75);
    margin-bottom: 15px;
    a {
      cursor: pointer;
      color: #3760EF;
    }
  }

  li::marker {
    color: #3760EF;
  }
}
